.game {
  border: solid 4px #bababa;
  height: 620px;
  width: 360px;
  margin: 40px auto;
  border-bottom-right-radius: 15% 10%;
  border-bottom-left-radius: 7.5% 5%;
}

@media screen and (max-width: 360px) {
  .game {
    border: none;
    width: 100%;
  }
}

.canvas-container {
  position: relative;
  border: solid 2px #bababa;
  border-radius: 4px;
  padding: 2px;
  margin: 20px auto;
  -ms-flex-pack: center;
      justify-content: center;
}

.canvas-overlay {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  background: #ccc;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.overlay-message {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: absolute;
  width: 100%;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  opacity: 1;
  color: tomato;
  font-size: 40px;
}

.overlay-button {
  border: solid 2px #2ec4b6;
  color: #2ec4b6;
  background: #fdfffc;
  padding: 5px 10px;
  margin-top: 20px;
  border-radius: 4px;
  font-size: 20px;
  cursor: pointer;
  outline: none;
}

.overlay-button:active {
  -webkit-transform: translateY(2px);
      -ms-transform: translateY(2px);
          transform: translateY(2px);
}

#canvas {
  background: #2ec4b6;
}

.controls {
  display: -ms-flexbox;
  display: flex;
  margin: 10px auto;
  padding: 2px;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.directional-container {
  background: #ccc;
  border: solid 4px #ccc;
  border-radius: 50%;
  width: 160px;
  height: 160px;
  padding: 8px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  outline: none;
}

.control {
  font-size: 18px;
  border: solid 2px #ccc;
  width: 80px;
  height: 80px;
  background: #dedede;
  outline: none;
}

.control:hover {
  background: #bbb;
}

.control-text {
  color: transparent;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.up {
  border-top: solid 4px #ccc;
  border-left: solid 4px #ccc;

  border-top-left-radius: 50%;
}

.up:active {
  -webkit-transform: translateX(-1px) translateY(-1px);
      -ms-transform: translateX(-1px) translateY(-1px);
          transform: translateX(-1px) translateY(-1px);
}

.right {
  border-top: solid 4px #ccc;
  border-right: solid 4px #ccc;

  border-top-right-radius: 50%;
}

.right:active {
  -webkit-transform: translateX(1px) translateY(-1px);
      -ms-transform: translateX(1px) translateY(-1px);
          transform: translateX(1px) translateY(-1px);
}

.down {
  border-bottom: solid 4px #ccc;
  border-right: solid 4px #ccc;

  border-bottom-right-radius: 50%;
}

.down:active {
  -webkit-transform: translateX(1px) translateY(1px);
      -ms-transform: translateX(1px) translateY(1px);
          transform: translateX(1px) translateY(1px);
}

.left {
  border-bottom: solid 4px #ccc;
  border-left: solid 4px #ccc;

  border-bottom-left-radius: 50%;
}

.left:active {
  -webkit-transform: translateX(-1px) translateY(1px);
      -ms-transform: translateX(-1px) translateY(1px);
          transform: translateX(-1px) translateY(1px);
}

.score {
  background: #accac7;
  color: white;
  text-align: right;
  font-size: 30px;
  padding: 15px 10px;
  width: 100px;
  height: 30px;
  margin: auto;
}



